  url('../fonts/SansPro/sourcesanspro-semibold-webfont.woff2') format('woff2'),
  url('../fonts/SansPro/sourcesanspro-semibold-webfont.woff') format('woff'),
  url('../fonts/SansPro/sourcesanspro-semibold-webfont.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;

}
